<template>
  <div class="scheduling-requests">
    <dashboard-table 
      :headers="headers"
      :items="items"
      :loading="loading"
      :page="currentPage"
      :qtdPages="pages"
      noResultText="Nenhum solicitação pendente"
      @changePage="changePage"
      @callAction="callAction"
    />
    <overlay ref="overlay" />
  </div>
</template>

<script>
import DashboardTable from './DashboardTable'
import Overlay from '@/components/globais/overlay/Overlay'
import BarberMixin from '@/mixins/BarberMixin'
import BarbershopMixin from '@/mixins/BarbershopMixin'
import DateMixin from '@/mixins/DateMixin'
import ScheduleMixin from '@/mixins/ScheduleMixin'
import SnackbarMixin from '@/mixins/SnackbarMixin'
import TextMixin from '@/mixins/TextMixin'

export default {
  name: 'DashboardSchedulingRequests',
  components: { DashboardTable, Overlay },
  mixins: [ BarberMixin, BarbershopMixin, DateMixin, ScheduleMixin, SnackbarMixin, TextMixin ],
  data: () => ({
    currentPage: null,
    headers: [
      { text: '', value: 'image', class: 'text-center', type: 'image' },
      { text: 'Nome do cliente', value: 'client', class: 'text-center', type: null },
      { text: 'Data', value: 'date', class: 'text-center', type: null },
      { text: 'Serviços', value: 'services', class: 'text-center', type: 'list' },
      { text: 'Valor', value: 'value', class: 'text-center', type: null },
      { text: '#', value: 'actions', class: 'text-center', type: 'action', width: '280px' }
    ],
    items: [],
    loading: true,
    pages: 1,
    interval: null
  }),
  computed: {
    hasBarberAndBarbershop () {
      return this.barberState && this.barbershopState ? true : false
    }
  },
  watch: {
    hasBarberAndBarbershop (val) {
      if (val) this.getSchedules()
    },
    qtdRequestsPendingState () {
      this.getSchedules()
    }
  },
  mounted () {
    const timeToSync = 3 * 60 * 1000
    this.interval = setTimeout(() => {
      if (this.hasBarberAndBarbershop) this.getSchedules()
    }, timeToSync)
    
    if (this.hasBarberAndBarbershop) this.getSchedules()
  },
  beforeDestroy () {
    this.interval = null
  },
  methods: {
    acept (item) {
      this.$refs.overlay.showOverlay('Salvando...')
      const errorMessage = 'Não foi possível aprovar o agendamento!'
      this.$axios.put(`/schedule/${item.id}/approve`)
      .then(response => {
        if (response.status === 200) {
          if (response.data.codigo === 200) {
            const index = this.items.findIndex(i => i.id === item.id)
            if (index > -1) {
              this.items.splice(index, 1)
              const qtdRequestsPending = this.items.length
              this.setQtdRequestsPendingState(qtdRequestsPending)
              this.$refs.overlay.hideOverlaySuccess('Cliente agendado!')
            } else this.$refs.overlay.hideOverlay()
          } else this.setError(response.data.corpo)
        } else this.setError(errorMessage)
      })
      .catch (() => this.setError(errorMessage))
    },
    callAction (action, item) {
      switch (action) {
        case 'acept':
          this.acept(item)
          break;
        case 'refuse':
          this.refuse(item)
          break;
      }
    },
    changePage (page) {
      this.currentPage = page
    },
    getSchedules () {
      this.loading = true
      let url = `/schedule/barbershop/${this.barber.barbershop_id}/pending?`
      if (this.barberState.id === this.barbershopState.admin_id) url += `all_requests=true&`
      if (this.currentPage) url += `page=${this.currentPage}`
      this.$axios.get(url)
      .then(response => {
        this.loading = false
        if (response.data.codigo === 200) {
          this.currentPage = response.data.corpo.current_page
          this.pages = response.data.corpo.last_page
          this.items = response.data.corpo.data.map(item => {
            const services = item.services.map(service => {
              return `${service.name}`
            })
            const disabled = new Date() > new Date(item.start_date)
            const actions = [
              { text: 'Aceitar', action: 'acept', class: 'success', disabled: disabled },
              { text: 'Recusar', action: 'refuse', class: 'default', disabled: disabled }
            ]
            return {
              id: item.id,
              date: this.formatDateTimeToStringPtBr(new Date(item.start_date)),
              image: item.user.image_url,
              client: item.user.name,
              value: `R$ ${this.setMoneyMask(Number(item.price).toFixed(2))}`,
              services: services,
              actions: actions 
            }
          })
        }
        else this.setError(response.data.corpo)
      })
      .catch (() => this.setError('Não foi possível recuperar os agendamentos!'))
    },
    refuse (item) {
      const errorMessage = 'Não foi possível reprovar o agendamento!'
      this.$axios.put(`/schedule/${item.id}/repprove`)
      .then(response => {
        if (response.status === 200) {
          if (response.data.codigo === 200) {
            this.showSnackbar(response.data.corpo)
            const index = this.items.findIndex(i => i.id === item.id)
            if (index > -1) this.items.splice(index, 1)
          } else this.setError(response.data.corpo)
        } else this.setError(errorMessage)
      })
      .catch (() => this.setError(errorMessage))
    },
    setError (err) {
      this.loading = false
      this.showSnackbarErro(err)
    }
  }
}
</script>